<template>
  <v-card
    height="100%"
  >
    <div
      id="gestion-docentes-dedicacion-tipo-instituto-porcentaje"
    >
      <v-card-title class="breakword">
        <v-row no-gutters>
          <v-col cols="11">
            Tipo de dedicaci√≥n por Instituto.
          </v-col>
          <v-col
            cols="1"
          >
            <v-tooltip
              bottom
            >
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  id="downloadBtn"
                  icon
                  fab
                  small
                  v-bind="attrs"
                  v-on="on"
                  @click="downloadIndicador('gestion-docentes-dedicacion-tipo-instituto-porcentaje')"
                >
                  <v-icon>{{ mdiDownload }}</v-icon>
                </v-btn>
              </template>
              <span>Descargar indicador.</span>
            </v-tooltip>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-subtitle>Distribuci√≥n de los/as docentes.</v-card-subtitle>
      <v-card-text>
        <v-responsive>
          <GChart
            id="charto"
            :data="barChartData"
            :options="barChartOptions"
            type="BarChart"
          />
        </v-responsive>
        <v-responsive style="width: 100%;">
          <v-simple-table
            dense
            class="cellheading mt-2"
          >
            <thead>
              <tr class="caption">
                <th>
                  Dedicaci√≥n
                </th>
                <th>
                  ICI
                </th>
                <th>
                  IDH
                </th>
                <th>
                  IDEI
                </th>
                <th>
                  ICO
                </th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>Exclusiva</td>
                <td>{{ dedicacionTipoInstitutosData.ICI.exc.per }}%</td>
                <td>{{ dedicacionTipoInstitutosData.IDH.exc.per }}%</td>
                <td>{{ dedicacionTipoInstitutosData.IDEI.exc.per }}%</td>
                <td>{{ dedicacionTipoInstitutosData.ICO.exc.per }}%</td>
              </tr>
              <tr>
                <td>Semi-exclusiva</td>
                <td>{{ dedicacionTipoInstitutosData.ICI.sem.per }}%</td>
                <td>{{ dedicacionTipoInstitutosData.IDH.sem.per }}%</td>
                <td>{{ dedicacionTipoInstitutosData.IDEI.sem.per }}%</td>
                <td>{{ dedicacionTipoInstitutosData.ICO.sem.per }}%</td>
              </tr>
              <tr>
                <td>Simple</td>
                <td>{{ dedicacionTipoInstitutosData.ICI.sim.per }}%</td>
                <td>{{ dedicacionTipoInstitutosData.IDH.sim.per }}%</td>
                <td>{{ dedicacionTipoInstitutosData.IDEI.sim.per }}%</td>
                <td>{{ dedicacionTipoInstitutosData.ICO.sim.per }}%</td>
              </tr>
            </tbody>
          </v-simple-table>
        </v-responsive>
      </v-card-text>
    </div>
  </v-card>
</template>

<script>
import { mapGetters, mapState } from 'vuex'
import { addPorcentajes } from '@/utils.js'
import { mdiDownload } from '@mdi/js'
export default {
  data: () => ({
    mdiDownload
  }),
  computed: {
    ...mapState(['dark', 'colors']),
    ...mapGetters(['docentes', 'mobileSize']),
    barChartData () {
      return {
        cols: [
          { id: 'Tipo de dedicaci√≥n por Instituto', label: 'Tipo de dedicaci√≥n por Instituto', type: 'string' },
          { type: 'string', role: 'tooltip', p: { html: true } },
          { id: 'Exclusiva', label: 'Exclusiva', type: 'number' },
          { role: 'annotation' },
          { id: 'Semi-exclusiva', label: 'Semi-exclusiva', type: 'number' },
          { role: 'annotation' },
          { id: 'Simple', label: 'Simple', type: 'number' },
          { role: 'annotation' }
        ],
        rows: [
          {
            c: [
              { v: 0, f: 'ICI' },
              { v: 0, f: this.createTooltip('ICI', this.dedicacionTipoInstitutosData.ICI) },
              {
                v: this.dedicacionTipoInstitutosData.ICI.exc.per,
                f: this.dedicacionTipoInstitutosData.ICI.exc.per
              },
              { v: 0, f: `${this.hiddeAnnotation(this.dedicacionTipoInstitutosData.ICI.exc.per)}` },
              {
                v: this.dedicacionTipoInstitutosData.ICI.sem.per,
                f: this.dedicacionTipoInstitutosData.ICI.sem.per
              },
              { v: 0, f: `${this.hiddeAnnotation(this.dedicacionTipoInstitutosData.ICI.sem.per)}` },
              {
                v: this.dedicacionTipoInstitutosData.ICI.sim.per,
                f: this.dedicacionTipoInstitutosData.ICI.sim.per
              },
              { v: 0, f: `${this.hiddeAnnotation(this.dedicacionTipoInstitutosData.ICI.sim.per)}` }
            ]
          },
          {
            c: [
              { v: 0, f: 'IDH' },
              { v: 0, f: this.createTooltip('IDH', this.dedicacionTipoInstitutosData.IDH) },
              {
                v: this.dedicacionTipoInstitutosData.IDH.exc.per,
                f: this.dedicacionTipoInstitutosData.IDH.exc.per
              },
              { v: 0, f: `${this.hiddeAnnotation(this.dedicacionTipoInstitutosData.IDH.exc.per)}` },
              {
                v: this.dedicacionTipoInstitutosData.IDH.sem.per,
                f: this.dedicacionTipoInstitutosData.IDH.sem.per
              },
              { v: 0, f: `${this.hiddeAnnotation(this.dedicacionTipoInstitutosData.IDH.sem.per)}` },
              {
                v: this.dedicacionTipoInstitutosData.IDH.sim.per,
                f: this.dedicacionTipoInstitutosData.IDH.sim.per
              },
              { v: 0, f: `${this.hiddeAnnotation(this.dedicacionTipoInstitutosData.IDH.sim.per)}` }
            ]
          },
          {
            c: [
              { v: 0, f: 'IDEI' },
              { v: 0, f: this.createTooltip('IDEI', this.dedicacionTipoInstitutosData.IDEI) },
              {
                v: this.dedicacionTipoInstitutosData.IDEI.exc.per,
                f: this.dedicacionTipoInstitutosData.IDEI.exc.per
              },
              { v: 0, f: `${this.hiddeAnnotation(this.dedicacionTipoInstitutosData.IDEI.exc.per)}` },
              {
                v: this.dedicacionTipoInstitutosData.IDEI.sem.per,
                f: this.dedicacionTipoInstitutosData.IDEI.sem.per
              },
              { v: 0, f: `${this.hiddeAnnotation(this.dedicacionTipoInstitutosData.IDEI.sem.per)}` },
              {
                v: this.dedicacionTipoInstitutosData.IDEI.sim.per,
                f: this.dedicacionTipoInstitutosData.IDEI.sim.per
              },
              { v: 0, f: `${this.hiddeAnnotation(this.dedicacionTipoInstitutosData.IDEI.sim.per)}` }
            ]
          },
          {
            c: [
              { v: 0, f: 'ICO' },
              { v: 0, f: this.createTooltip('ICO', this.dedicacionTipoInstitutosData.ICO) },
              {
                v: this.dedicacionTipoInstitutosData.ICO.exc.per,
                f: this.dedicacionTipoInstitutosData.ICO.exc.per
              },
              { v: 0, f: `${this.hiddeAnnotation(this.dedicacionTipoInstitutosData.ICO.exc.per)}` },
              {
                v: this.dedicacionTipoInstitutosData.ICO.sem.per,
                f: this.dedicacionTipoInstitutosData.ICO.sem.per
              },
              { v: 0, f: `${this.hiddeAnnotation(this.dedicacionTipoInstitutosData.ICO.sem.per)}` },
              {
                v: this.dedicacionTipoInstitutosData.ICO.sim.per,
                f: this.dedicacionTipoInstitutosData.ICO.sim.per
              },
              { v: 0, f: `${this.hiddeAnnotation(this.dedicacionTipoInstitutosData.ICO.sim.per)}` }
            ]
          }
        ]
      }
    },
    barChartOptions () {
      return {
        chartArea: {
          left: 40,
          right: 40,
          top: 40,
          bottom: 20
        },
        // colors: state.colores.investigadoresSexo,
        focusTarget: 'category',
        tooltip: {
          isHtml: true
          // textStyle: { color: 'white', bold: false }
        },
        colors: this.colors,
        // title: '',
        backgroundColor: { fill: 'transparent' },
        height: 350,
        isStacked: 'percent',
        legend: {
          position: 'top',
          maxLines: 3,
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        },
        bar: { groupWidth: '50%' },
        annotations: {
          // style: 'line'
          highContrast: false,
          textStyle: {
            color: 'white',
            //   opacity: 0.9,
            fontSize: 13
          }
        },
        hAxis: {
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
            // fontSize: 14
          },
          slantedText: false
        },
        vAxis: {
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        }
      }
    },
    dedicacionTipoInstitutosData () {
      const dedicacionInstitutos = this.docentes.reduce((acc, cur) => {
        const exclusiva = cur.descripcion_dedicacion === 'Exclusiva'
        const semiexclusiva = cur.descripcion_dedicacion === 'Semi-Exclusiva' || cur.descripcion_dedicacion === 'Semi - Excl Conven'
        const simple = cur.descripcion_dedicacion === 'Simple'
        return cur.unidad_academica === 'ICI'
          ? {
            ...acc,
            ICI: exclusiva
              ? { ...acc.ICI, exc: acc.ICI.exc + 1 }
              : semiexclusiva
                ? { ...acc.ICI, sem: acc.ICI.sem + 1 }
                : simple
                  ? { ...acc.ICI, sim: acc.ICI.sim + 1 }
                  : acc
          }
          : cur.unidad_academica === 'IDH'
            ? {
              ...acc,
              IDH: exclusiva
                ? { ...acc.IDH, exc: acc.IDH.exc + 1 }
                : semiexclusiva
                  ? { ...acc.IDH, sem: acc.IDH.sem + 1 }
                  : simple
                    ? { ...acc.IDH, sim: acc.IDH.sim + 1 }
                    : acc
            }
            : cur.unidad_academica === 'IDEI'
              ? {
                ...acc,
                IDEI: exclusiva
                  ? { ...acc.IDEI, exc: acc.IDEI.exc + 1 }
                  : semiexclusiva
                    ? { ...acc.IDEI, sem: acc.IDEI.sem + 1 }
                    : simple
                      ? { ...acc.IDEI, sim: acc.IDEI.sim + 1 }
                      : acc
              }
              : cur.unidad_academica === 'ICO'
                ? {
                  ...acc,
                  ICO: exclusiva
                    ? { ...acc.ICO, exc: acc.ICO.exc + 1 }
                    : semiexclusiva
                      ? { ...acc.ICO, sem: acc.ICO.sem + 1 }
                      : simple
                        ? { ...acc.ICO, sim: acc.ICO.sim + 1 }
                        : acc
                }
                : acc
      },
      {
        ICI: {
          exc: 0,
          sem: 0,
          sim: 0
        },
        IDH: {
          exc: 0,
          sem: 0,
          sim: 0
        },
        IDEI: {
          exc: 0,
          sem: 0,
          sim: 0
        },
        ICO: {
          exc: 0,
          sem: 0,
          sim: 0
        }
      }
      )
      return this.addPorcentajes(dedicacionInstitutos)
    }
  },
  methods: {
    downloadIndicador (indicador) {
      this.$store.dispatch('createPng', indicador)
    },
    addPorcentajes (v) {
      return {
        ICI: addPorcentajes(v.ICI, v.ICI.exc + v.ICI.sem + v.ICI.sim),
        IDH: addPorcentajes(v.IDH, v.IDH.exc + v.IDH.sem + v.IDH.sim),
        IDEI: addPorcentajes(v.IDEI, v.IDEI.exc + v.IDEI.sem + v.IDEI.sim),
        ICO: addPorcentajes(v.ICO, v.ICO.exc + v.ICO.sem + v.ICO.sim)
      }
    },
    createTooltip (title, value) {
      return `<div style="min-width: 370px; padding: 10px 10px 10px 10px; font-size: 13px; color: black;">
      <h3>${title}</h3>
      <ul>
      <li>Exclusiva: ${value.exc.per}% (${value.exc.abs})</li>
      <li>Semi-exclusiva: ${value.sem.per}% (${value.sem.abs})</li>
      <li>Simple: ${value.sim.per}% (${value.sim.abs})</li>
      </li>
      </ul></div>`
    },
    hiddeAnnotation (v) {
      return v <= 6 ? '' : `${v}%`
    }
  }
}
</script>
